<template>
  <Row :gutter="16">
    <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="8" class="ivu-mb">
      <Card shadow :padding="0">
        <template #title>
          <p>
            <Icon type="md-options" />
            <span class="ivu-ml-4">账户设置</span>
          </p>
        </template>
        <CellGroup class="ivu-pt-8 ivu-pb-8" @on-click="handleClick">
          <Cell name="Safety" :selected="selected('Safety')" title="安全设置" label="密码、邮箱等设置" />
          <Cell name="Notice" :selected="selected('Notice')" title="消息通知" label="各种通知的设置" />
          <Cell name="Account" :selected="selected('Account')" title="账号绑定" label="绑定第三方社交账户" />
        </CellGroup>
      </Card>
    </Col>
    <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="16" class="ivu-mb">
      <Card shadow>
        <transition :name="routeAnimate" mode="out-in">
          <component :is="component" />
        </transition>
      </Card>
    </Col>
  </Row>
</template>

<script>
  import Safety from './component/Safety'
  import Notice from './component/Notice'
  import Account from './component/Account'

  export default {
    name: 'UserSetting',
    components: { Safety, Notice, Account },
    data() {
      return {
        component: 'Safety'
      }
    },
    computed: {
      routeAnimate() {
        return this.$store.state.app.routeAnimate
      },
      selected() {
        return name => name === this.component
      }
    },
    methods: {
      handleClick(name) {
        this.component = name
      }
    }
  }
</script>
