<template>
  <Row :gutter="16">
    <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="8" class="ivu-mb">
      <Card shadow v-height="'100%'">
        <template #title>
          <Avatar size="small" icon="md-person" style="color: #1890ff; background: #e6f7ff" />
          <span class="ivu-pl-8">个人信息</span>
        </template>
        <div class="component-user-avatar">
          <Avatar :src="userInfo.avatar" />
          <div class="component-user-avatar-cover" @click="$refs.avatarModal.edit()">
            <Icon type="ios-camera" :size="42" />
          </div>
        </div>
        <List class="ivu-mt" size="small">
          <ListItem>
            <ListItemMeta>
              <template #title>
                <Icon type="md-person" />
                <span class="ivu-typography ivu-pl-8">用户名称</span>
              </template>
            </ListItemMeta>
            <template #extra>
              <span class="ivu-typography">{{ userInfo.username }} / {{ userInfo.name }}</span>
            </template>
          </ListItem>
          <ListItem>
            <ListItemMeta>
              <template #title>
                <Icon type="md-phone-portrait" />
                <span class="ivu-typography ivu-pl-8">手机号码</span>
              </template>
            </ListItemMeta>
            <template #extra>
              <span class="ivu-typography">{{ userInfo.telephone }}</span>
            </template>
          </ListItem>
          <ListItem>
            <ListItemMeta>
              <template #title>
                <Icon type="md-mail" />
                <span class="ivu-typography ivu-pl-8">用户邮箱</span>
              </template>
            </ListItemMeta>
            <template #extra>
              <span class="ivu-typography">{{ userInfo.email }}</span>
            </template>
          </ListItem>
          <ListItem>
            <ListItemMeta>
              <template #title>
                <Icon type="md-people" />
                <span class="ivu-typography ivu-pl-8">所属部门</span>
              </template>
            </ListItemMeta>
            <template #extra>
              <span class="ivu-typography">{{ userInfo.deptName }}</span>
            </template>
          </ListItem>
          <ListItem>
            <ListItemMeta>
              <template #title>
                <Icon type="md-body" />
                <span class="ivu-typography ivu-pl-8">所属角色</span>
              </template>
            </ListItemMeta>
            <template #extra>
              <span class="ivu-typography">{{ userInfo.roles }}</span>
            </template>
          </ListItem>
          <ListItem>
            <ListItemMeta>
              <template #title>
                <Icon type="md-calendar" />
                <span class="ivu-typography ivu-pl-8">创建日期</span>
              </template>
            </ListItemMeta>
            <template #extra>
              <span class="ivu-typography">{{ userInfo.createTime }}</span>
            </template>
          </ListItem>
        </List>
      </Card>
    </Col>
    <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="16" class="ivu-mb">
      <Card shadow v-height="'100%'" :padding="0">
        <Tabs name="name1" class="ivu-mb">
          <TabPane :label="h => tabPaneLabel(h, '基本信息', 'ios-create')" name="name1">
            <div class="ivu-p">
              <Form ref="form1" :model="form1" :rules="rule1" :label-width="80">
                <FormItem prop="userName" label="昵称">
                  <Input v-model="form1.userName" placeholder="请输入昵称" />
                </FormItem>
                <FormItem prop="telephone" label="电话">
                  <Input v-model="form1.telephone" :maxlength="11" show-word-limit placeholder="请输入电话" />
                </FormItem>
                <FormItem prop="email" label="邮箱">
                  <Input v-model="form1.email" placeholder="请输入邮箱" />
                </FormItem>
                <FormItem label="性别">
                  <Select v-model="form1.gender" placeholder="请选择性别">
                    <Option :value="1">男</Option>
                    <Option :value="2">女</Option>
                    <Option :value="3">保密</Option>
                  </Select>
                </FormItem>
                <FormItem label="简介">
                  <Input type="textarea" v-model="form1.intro" :rows="4" maxlength="200" show-word-limit placeholder="请输入个人简介" />
                </FormItem>
                <FormItem>
                  <Space>
                    <Button @click="handleSubmit('form1')" type="primary">提交</Button>
                    <Button @click="handleReset('form1')">重置</Button>
                  </Space>
                </FormItem>
              </Form>
            </div>
          </TabPane>
          <TabPane :label="h => tabPaneLabel(h, '修改密码', 'ios-lock')" name="name2">
            <div class="ivu-p">
              <Form ref="form2" :model="form2" :rules="rule2" :label-width="80">
                <FormItem prop="oldPassword" label="旧密码">
                  <Input v-model="form2.oldPassword" type="password" password placeholder="请输入旧密码" />
                </FormItem>
                <FormItem prop="newPassword" label="新密码">
                  <Input v-model="form2.newPassword" type="password" password placeholder="请输入新密码" />
                </FormItem>
                <FormItem prop="confirmPassword" label="确认密码">
                  <Input v-model="form2.confirmPassword" type="password" password placeholder="请确认新密码" />
                </FormItem>
                <FormItem>
                  <Space>
                    <Button @click="handleSubmit('form2')" type="primary">提交</Button>
                    <Button @click="handleReset('form2')">重置</Button>
                  </Space>
                </FormItem>
              </Form>
            </div>
          </TabPane>
        </Tabs>
      </Card>
    </Col>
    <AvatarCropper :avatarPath="userInfo.avatar" ref="avatarModal" />
  </Row>
</template>

<script>
  import { resolveComponent } from 'vue'
  import AvatarCropper from './component/AvatarCropper'

  export default {
    name: 'UserProfile',
    components: { AvatarCropper },
    data() {
      return {
        form1: {
          userName: '管理员',
          telephone: '18888888887',
          email: 'admin@istudy.run',
          gender: '1',
          intro: ''
        },
        rule1: {
          userName: [{ required: true, message: '用户昵称不能为空', trigger: 'change' }],
          telephone: [{ required: true, message: '手机号码不能为空', trigger: 'change' }],
          email: [{ required: true, message: '邮箱地址不能为空', trigger: 'change' }]
        },
        form2: {
          oldPassword: '',
          newPassword: '',
          confirmPassword: ''
        },
        rule2: {
          oldPassword: [{ required: true, message: '旧密码不能为空', trigger: 'change' }],
          newPassword: [{ required: true, message: '新密码不能为空', trigger: 'change' }],
          confirmPassword: [{ required: true, message: '确认密码不能为空', trigger: 'change' }]
        }
      }
    },
    computed: {
      userInfo() {
        return this.$store.state.user.userInfo
      }
    },
    created() {
      const { name } = this.userInfo
      this.form1 = { ...this.userInfo, userName: name }
    },
    methods: {
      tabPaneLabel(h, text, icon) {
        const avatar = h(resolveComponent('Avatar'), {
          icon,
          size: 'small',
          class: 'ivu-mr-8',
          style: 'color: #722ed1; background: #f9f0ff;'
        })
        return h('span', {}, [avatar, text])
      },
      handleReset(name) {
        this.$refs[name].resetFields()
      },
      handleSubmit(name) {
        this.$refs[name].validate(valid => {
          if (valid) {
            this.$Message.success('Success!')
          } else {
            this.$Message.error('Fail!')
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  :deep(.ivu-tabs-nav) {
    display: flex;
    height: 53px;
    margin-left: 24px;

    .ivu-tabs-tab {
      display: flex;
      align-items: center;

      .ivu-icon {
        margin-right: 0;
      }
    }
  }
</style>
