<template>
  <Modal title="修改头像" :modelValue="visible" :mask-closable="false" :width="700" ok-text="提交" @on-cancel="onCancel" @on-ok="finish('blob')">
    <Row>
      <Col :xs="24" :md="12" style="height: 350px">
        <VueCropper
          ref="cropper"
          :img="options.img"
          :autoCrop="options.autoCrop"
          :autoCropWidth="options.autoCropWidth"
          :autoCropHeight="options.autoCropHeight"
          :fixedBox="options.fixedBox"
          :original="options.original"
          :centerBox="options.centerBox"
          @realTime="realTime"
        />
      </Col>
      <Col :xs="24" :md="12" style="height: 350px">
        <div class="avatar-cropper-preview">
          <img :src="previews.url" :style="previews.img" />
        </div>
      </Col>
    </Row>
    <br />
    <Row>
      <Col :xs="24" :md="12">
        <Row>
          <Col span="12">
            <Upload name="file" action="action" accept=".jpeg,.jpg,.png" :before-upload="beforeUpload" :show-upload-list="false">
              <Button icon="ios-camera">选择图片</Button>
            </Upload>
          </Col>
          <Col span="12" class="avatar-action">
            <Tooltip content="放大" theme="light" placement="top" transfer>
              <Button icon="md-add" @click="changeScale(1)" />
            </Tooltip>
            <Tooltip content="缩小" theme="light" placement="top" transfer>
              <Button icon="md-remove" @click="changeScale(-1)" />
            </Tooltip>
            <Tooltip content="旋转" theme="light" placement="top" transfer>
              <Button icon="md-refresh" @click="rotateRight" />
            </Tooltip>
          </Col>
        </Row>
      </Col>
    </Row>
  </Modal>
</template>
<script>
  import 'vue-cropper/dist/index.css'
  import { VueCropper } from 'vue-cropper'

  export default {
    name: 'AvatarCropper',
    components: { VueCropper },
    props: {
      avatarPath: {
        type: String,
        default: '',
        required: true
      }
    },
    data() {
      return {
        visible: false,
        uploading: false,
        options: {
          /**
           * 更多参数 @see https://github.com/xyxiao001/vue-cropper
           */
          img: '', // 裁剪图片的地址
          autoCrop: true, // 是否默认生成截图框
          autoCropWidth: 200, // 默认生成截图框宽度
          autoCropHeight: 200, // 默认生成截图框高度
          fixed: true, // 是否开启截图框宽高固定比例
          fixedBox: true, // 固定截图框大小
          centerBox: false, // 截图框是否被限制在图片里面
          original: false // 上传图片按照原始比例渲染
        },
        previews: {}
      }
    },
    methods: {
      edit() {
        this.options.img = this.avatarPath
        this.visible = true
      },
      onCancel() {
        this.options.img = ''
        this.visible = false
      },
      changeScale(num) {
        num = num || 1
        this.$refs.cropper.changeScale(num)
      },
      rotateLeft() {
        this.$refs.cropper.rotateLeft()
      },
      rotateRight() {
        this.$refs.cropper.rotateRight()
      },
      beforeUpload(file) {
        const reader = new FileReader()
        // 把Array Buffer转化为blob 如果是base64不需要
        // 转化为base64
        reader.readAsDataURL(file)
        reader.onload = () => {
          this.options.img = reader.result
        }
        // 转化为blob
        // reader.readAsArrayBuffer(file)

        return false
      },

      // 上传图片（点击上传按钮）
      finish(type) {
        const formData = new FormData()
        // 输出
        if (type === 'blob') {
          this.$refs.cropper.getCropBlob(data => {
            const img = window.URL.createObjectURL(data)
            this.model = true
            this.modelSrc = img
            formData.append('file', data, this.fileName)
            this.$axios
              .post('https://www.mocky.io/v2/5cc8019d300000980a055e76', formData, {
                contentType: false,
                processData: false,
                headers: {
                  'Content-Type': 'application/x-www-form-urlencoded'
                }
              })
              .then(response => {
                console.log('upload response:', response)
                // var res = response.data
                // if (response.status === 'done') {
                //   _this.imgFile = ''
                //   _this.headImg = res.realPathList[0] // 完整路径
                //   _this.uploadImgRelaPath = res.relaPathList[0] // 非完整路径
                //   _this.$Message.success('上传成功')
                //   this.visible = false
                // }
                this.$Message.success('上传成功')
                this.$emit('ok', response.url)
                this.visible = false
              })
          })
        } else {
          this.$refs.cropper.getCropData(data => {
            this.model = true
            this.modelSrc = data
          })
        }
      },
      realTime(data) {
        this.previews = data
      }
    }
  }
</script>

<style lang="less" scoped>
  .avatar-cropper-preview {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 200px;
    height: 200px;
    border-radius: 50%;
    box-shadow: 0 0 4px #ccc;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .avatar-action {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
</style>
